*{
    padding:0;
    margin: 0;
    font-family: 'Courier New', Courier, monospace;
}
h1{
    text-align: center;
    background-color: rgb(66, 64, 64);
    color:white;
}
.git{
    position: absolute;
    right: 10px;
}
.github{
        color: white;
}
a{
    text-decoration: none;
    color:rgb(89, 87, 102);
}
a:active{
    color:black;
    border-bottom: 3px solid black;
}
a:hover{
    color:purple;
    /*
    border-radius: 5px;
    box-shadow: 3px 10px 5px rgba(0, 0, 0, 0.4);
   */
}

.header{
    text-align: center;
    padding:10px 30px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 19px;
    color: royalblue;
    text-shadow: 1px 3px rgba(225, 226, 226, 0.5);
}
.btn{
    display: inline;
    margin: 0px 20px;
    padding-top:3px;
}
.input{
    display: inline;
}
.tracks{
    display: flex;
    flex-wrap: wrap;
    padding:10px 35px;
    border:1.5px solid rgb(211, 199, 199);
    border-radius: 4px;
}
.track{
    position: relative;
    border:1px solid gray;
    border-radius: 5px;
    margin: 10px 20px;
    padding:15px 10px;
    width:47%;
    box-sizing: border-box;
   
}
.track:hover{
     box-shadow: 5px 8px 10px rgba(27, 214, 199, 0.5);
}
.song{
    color:rebeccapurple;
    font-weight: 600;
}
.info{
    color:rgb(243, 6, 6);
    font-weight: 400;
}
.lyrics{
    padding:15px;
    margin: 5px 14px;
    border: .5px solid grey;
    border-radius: 4px;
}

@media (max-width:700px){
    .tracks{
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
    }
    .track{
        width:90%;
    }
}